.grid {
    list-style-type: none;
    gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.title {
    color: lightgray;
    margin-top: 20px;
}

.titleDiv {
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
}
